<script>
import imagesLoaded from "vue-images-loaded";
export default {
  directives: {
    imagesLoaded,
  },
  data() {
    const item = {
      location: "50,50",
      id: "1234567",
      score: "100",
    };
    
    return {
      showSearch: false,
      tableHeight: '450px',
      searchForm: {
        photoName: '',
        processType: "photo",
        startTime: null,
        endTime: null,
        kindCount: null,
      },
      photo_name: "",
      process_type: "",
      upload_time: "",
      process_time: "",
      number: 0,
      kind: 0,
      isDragging: false, // 用于追踪拖动状态
      offsetX: 0,
      offsetY: 0,
      currentPage: 1,  // 当前页码，el-pagination 组件的绑定
      pageNum: 1,      // 用于手动控制页码的变量
      pageSize: 3,     // 每页显示多少条数据
      total: 0,        // 总数据条数
      tableData: {
        photos: [],     // 存放所有照片数据
      },
      isVideo: false, // 用来判断是视频还是图片
      total: 0,
      size: false,
      detail: false,
      fastElimination: false,
      dark: false,
      CLAHE: false,
      Sharpening: false,
      isShowPhoto: false,
      drawerVisible: false,
      activeIndex: "4",
      showProgress: false,
      progressPercentage: 0,
      photoName: "",
      photoUrl_: "", //本地图片路径,
      isShowLocalPhoto: true,
      uploadUrl: "",
      photoUrl: "",
      detailPhotoName: "",
      logDetail: false,
    };
  },
  watch: {
  showSearch(newValue) {
    if (newValue) {
      // 如果下拉窗格显示，增加表格的高度
      this.tableHeight = '1600px';
    } else {
      // 如果下拉窗格隐藏，恢复表格的正常高度
      this.tableHeight = '450px';
    }
  },
},
  created() {
    // 在生命周期钩子里初始化 cameraUrl
    this.uploadUrl = this.$apiBaseUrl + "/upload_photo";
  },
  computed: {

    paginatedData() {
      if (!this.tableData || !Array.isArray(this.tableData.photos)) return [];

      const photos = this.tableData.photos;  // 获取 photos 数组
      const start = (this.currentPage - 1) * this.pageSize;
      const end = this.currentPage * this.pageSize;

      return photos.slice(start, end); // 对 photos 数组进行分页
    },
    drawer_class_ctrl() {
      return this.drawerVisible ? "drawer-open" : "drawer-close";
    },
    drawer_button_class_ctrl() {
      return [
        this.drawerVisible ? "drawer-button-open" : "drawer-button-close",
        this.drawerVisible ? "el-icon-caret-right" : "el-icon-caret-left",
      ];
    },
  },
  mounted() {
    this.total = this.tableData.length; // 设置总数据条目数
    this.$notify({
      title: '提示',
      message: '系统特性:经过测试单独开始某些功能很卡，但是如果混合加入任一追踪或检测，速度就会很快，这一问题我们正在排查中，如果您觉得太慢请开启追踪或检测让服务器释放性能',
      type: 'warning'
    });
  },
  methods: {

    handlePageChange(newPage) {
      this.currentPage = newPage;  // 更新 currentPage
      this.pageNum = newPage;      // 同步 pageNum
    },

    handlePageSizeChange(newSize) {
      this.pageSize = newSize;     // 更新每页显示条数
      this.currentPage = 1;        // 每次更改页大小时，返回第一页
      this.pageNum = 1;            // 同步 pageNum
    },
    onSearch() {
      const form = this.searchForm;
      const params = {};
      if (form.photoName) {
        params.photo_name = form.photoName;
      }
      if (form.processType) {
        params.source = form.processType;
      }
      if (form.startTime) {
        params.start_time = form.startTime;
      }
      if (form.endTime) {
        params.end_time = form.endTime;
      }
      if (form.kindCount != null && form.kindCount !== '') {
        params.num_defects = form.kindCount;
      }

      console.log(form.photoName);
      console.log(form.processType);
      console.log(form.startTime);
      console.log(form.endTime);
      console.log(form.kindCount);
      console.log(params);

      this.$axios.post(this.$apiBaseUrl + "/query", params)
        .then(res => {
          const labelColors = {
            1: { color: 'rgb(0, 0, 255)', label: '斑点' },
            2: { color: 'rgb(0, 255, 0)', label: '划痕' },
            3: { color: 'rgb(255, 0, 0)', label: '表面缺陷' },
            4: { color: 'rgb(0, 255, 255)', label: '其他' }
          };

          this.tableData.photos = res.data.photos.map(item => {
            // 转换 process_type（字符串转数组）
            if (item.process_type && typeof item.process_type === 'string') {
              item.process_type = JSON.parse(item.process_type.replace(/'/g, '"'));
            }

            // defect_info 处理
            if (item.defect_info && typeof item.defect_info === 'object') {
              item.num_defects = item.defect_info.num_defects || 0;

              const rawLabels = Array.isArray(item.defect_info.detected_labels)
                ? item.defect_info.detected_labels.filter(label => label !== 0)
                : [];

              item.detected_labels = rawLabels.join(', ');
              item.color_labels = rawLabels.map(label => labelColors[label]).filter(Boolean);
            } else {
              item.num_defects = 0;
              item.detected_labels = '';
              item.color_labels = [];
            }

            return item;
          });

          this.total = this.tableData.photos.length;
        })
        .catch(error => {
          console.error("请求失败", error);
        });



    },

    onReset() {
      this.searchForm = {
        photoName: '',
        processType: "photo",
        startTime: null,
        endTime: null,
        kindCount: null,
      };
      this.tableData.photos = [];
    },
    handleSizeChange() {

    },
    handleCurrentChange() {

    },
    startDrag(event) {
      this.isDragging = true; // 开始拖动
      this.offsetX =
        event.clientX - this.$refs.draggableBox.getBoundingClientRect().left;
      this.offsetY =
        event.clientY - this.$refs.draggableBox.getBoundingClientRect().top;

      document.addEventListener("mousemove", this.doDrag);
      document.addEventListener("mouseup", this.stopDrag);
    },
    doDrag(event) {
      if (this.isDragging) {
        const left = event.clientX - this.offsetX;
        const top = event.clientY - this.offsetY;

        this.$refs.draggableBox.style.left = `${left}px`;
        this.$refs.draggableBox.style.top = `${top}px`;
        this.$refs.draggableBox.style.transform = "none"; // 移动时取消 `transform`
      }
    },
    stopDrag() {
      this.isDragging = false;
      document.removeEventListener("mousemove", this.doDrag);
      document.removeEventListener("mouseup", this.stopDrag);
    },
    closeWindow() {
      this.logDetail = false; // 设置为 false 以关闭窗口
    },


    async handleButtonClick(photoName) {
      console.log(photoName);
      try {
        const response = await fetch(
          this.$apiBaseUrl + `/stream_photo?name=${photoName}&style=2`
        );

        if (!response.ok) {
          throw new Error("Network response was not ok");
        }

        console.log(response.url);

        this.detailPhotoUrl = response.url;
        console.log(this.detailPhotoUrl);
        this.logDetail = true;
        console.log(this.logDetail);

      } catch (error) {
        console.error("There was a problem with the fetch operation:", error);
      }
    },
    savePhoto() {
      if (!this.photoName) {
        this.$message({
          type: "error",
          message: "未上传图片",
        });
      } else {
        fetch(this.photoUrl)
          .then((response) => response.blob()) // 将响应转换为 Blob
          .then((blob) => {
            // 创建一个临时 URL
            const url = URL.createObjectURL(blob);

            // 创建一个隐藏的 <a> 元素用于下载
            const a = document.createElement("a");
            a.style.display = "none";
            a.href = url;
            a.download = "downloaded-image.jpg"; // 下载时的文件名

            // 将 <a> 元素添加到 DOM 中并触发点击事件
            document.body.appendChild(a);
            a.click();

            // 释放 URL 对象并移除 <a> 元素
            URL.revokeObjectURL(url);
            document.body.removeChild(a);
          })
          .catch((error) => {
            this.$message({
              type: "error",
              message: "未进行预测",
            });
          });
      }
    },
    imageProgress(instance, image) {
      const result = image.isLoaded ? "loaded" : "broken";
    },
    sendParameters() {
      let data = {
        size: this.size,
        detail: this.detail,
        fastElimination: this.fastElimination,
        dark: this.dark,
        CLAHE: this.CLAHE,
        Sharpening: this.Sharpening,
      };
      console.log(this.detail);
      console.log(this.fastElimination);


      return this.$axios
        .post(this.$apiBaseUrl + "/ConfirmParams", data)
        .then((res) => {

        });
    },
    // checkParameter(value) {
    //   var that = this;
    //   if (value) {
    //     // 有东西开启了，要保证额外功能开启的时候，保证追踪或者检测开启
    //     const people_list = [that.people_attribute_enable];
    //     for (let i = 0; i < people_list.length; i++) {
    //       if (people_list[i]) {
    //         that.people_tracker_enable = true;
    //       }
    //     }
    //     const vehicle_list = [
    //       that.vehicle_attribute_enable,
    //       that.vehicle_license_enable,
    //       that.vehicle_press_detector_enable,
    //       that.vehicle_invasion_enable,
    //     ];
    //     for (let i = 0; i < vehicle_list.length; i++) {
    //       if (vehicle_list[i]) {
    //         that.vehicle_tracker_enable = true;
    //       }
    //     }
    //   } else {
    //     // 检测关闭，额外功能也要关闭
    //     if (!(that.people_detector_enable && that.people_tracker_enable)) {
    //       that.people_attribute_enable = false;
    //     }
    //     if (!(that.vehicle_detector_enable && that.vehicle_tracker_enable)) {
    //       that.vehicle_attribute_enable = false;
    //       that.vehicle_license_enable = false;
    //       that.vehicle_press_detector_enable = false;
    //       that.vehicle_invasion_enable = false;
    //     }
    //   }
    // },
    async dealwithPhoto() {
      let loadingInstance; // 在 try 块外定义 loadingInstance，以便在 catch 中访问
      console.log("123");

      try {
        // 显示 loading
        loadingInstance = this.$loading({
          lock: true,
          text: `加载中...`, // 去掉百分比显示
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)",
        });

        // 发送处理参数
        await this.sendParameters();

        let data = {
          name: this.photoName,
        };
        console.log(data);

        // 开始处理照片
        await this.$axios.post(this.$apiBaseUrl + "/start_process_photo", data);


        // 获取照片和日志
        await this.getPhoto();
        // await this.getLog();
      } catch (error) {
        console.error("处理过程出错:", error);
      } finally {
        // 确保 loading 被关闭
        if (loadingInstance) {
          loadingInstance.close();
        }
      }
    },


    resetPhoto() {
      this.isShowPhoto = false;
      this.isShowLocalPhoto = true;
      this.photoName = "";
      this.photoUrl_ = "";
    },
    async getPhoto() {
      let flag = 0; // 初始化标志变量
      const checkInterval = 1000; // 轮询间隔，单位为毫秒

      while (flag === 0) {
        try {
          const response = await fetch(this.$apiBaseUrl + `/stream_photo?name=${this.photoName}&style=2`);

          if (!response.ok) {
            throw new Error("Network response was not ok");
          }

          const url = response.url; // 获取响应的 URL

          // 发送请求来检查 url 指向的内容
          const contentResponse = await fetch(url); // 获取 url 指向的内容

          if (!contentResponse.ok) {
            throw new Error("Failed to fetch content from the URL");
          }

          const contentType = contentResponse.headers.get("Content-Type");

          if (contentType && contentType.includes("application/json")) {
            // 如果返回的是 JSON，则表示图片未准备好
            const jsonResponse = await contentResponse.json(); // 解析 JSON 响应
            console.log(jsonResponse); // 查看 JSON 内容
            // 检查 JSON 内容是否包含 success 字段并且值为 0
            if (jsonResponse.success === 0) {
              console.log("图片还在处理中...");
              flag = 0; // 图片未准备好，继续轮询
            }
          } else if (contentType && contentType.includes("image/jpeg")) {
            // 如果返回的是图像，则表示图片已准备好
            this.photoUrl = url; // 使用 URL 赋值给图片的 src
            console.log("图片已准备好，URL:", this.photoUrl);
            flag = 1; // 图片已准备好，设置标志位为 1
            this.isShowPhoto = true; // 显示图片
            this.isShowLocalPhoto = false; // 隐藏本地图片
          } else {
            console.log("未知的响应类型");
          }
        } catch (error) {
          console.error("获取照片时发生错误:", error);
        }

        // 等待指定的时间再进行下一次轮询
        await new Promise(resolve => setTimeout(resolve, checkInterval));
      }
    },

    toggleDrawer() {
      this.drawerVisible = !this.drawerVisible;
    },
    handleBeforeUpload(file) {
      const supportedFormats = ["jpg", "jpeg", "png", "gif"];
      const fileExtension = file.name.split(".").pop().toLowerCase();
      const isSupportedFormat = supportedFormats.includes(fileExtension);

      const maxSize = 100 * 1024 * 1024; // 100 MB
      const isSizeValid = file.size <= maxSize;

      if (isSupportedFormat && isSizeValid) {
        this.showConfirmDialog(file);
        return false; // 阻止自动上传
      } else {
        let errorMessage = "";
        if (!isSupportedFormat) {
          errorMessage += "不支持的图片格式。";
        }
        if (!isSizeValid) {
          errorMessage += "文件大小超过 100 MB。";
        }
        this.$message.error(errorMessage);
        return false;
      }
    },
    showConfirmDialog(file) {
      this.$confirm("确认上传此文件？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.uploadFile(file);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消上传",
          });
        });
    },
    uploadFile(file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        this.isShowPhoto = true;
        this.isShowLocalPhoto = true;
        this.photoUrl_ = e.target.result; // 将图片的 data URL 设置为 photoUrl
      };
      reader.readAsDataURL(file);

      const formData = new FormData();
      formData.append("photo", file);

      this.$axios
        .post(this.uploadUrl, formData, {
          headers: {
            'Authorization': 'token5370ffbdcb08f3a7bfbf75287582539842d93c30', // 添加 Authorization 头
            'Content-Type': 'multipart/form-data' // 确保使用 multipart/form-data
          }
        })
        .then((response) => {
          this.handleSuccess(response, file);
        })
        .catch((error) => {
          this.handleError(error, file);
        });
    },
    handleSuccess(response, file) {
      this.$message({
        type: "success",
        message: "上传成功!",
      });
      this.photoName = response.data.photoname;
    },
    handleError(error, file) {
      this.$message({
        type: "error",
      });
      console.error("上传失败:", error);
    },
    handleProgress(event, file) {
      console.log("上传进度:", event.percent);
    },
    onImageLoad() {
      console.log("图片加载成功");
    },
    onImageError() {
      console.error("图片加载失败");
    },
  },
};
</script>

<style scoped>
.right-log {
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 100%;
  background-color: white;
  margin-left: 2%;
  margin-right: 10%;
  border-radius: 8px;
}

.right-log-head {
  height: 5vh;
  width: 100%;
  background-color: rgb(66, 159, 255);
  color: white;
  font-size: 1.5vw;
  line-height: 5vh;
  border-top-right-radius: 8px;
  /* 可选: 让边角变圆 */
  border-top-left-radius: 8px;
}

.bottom-ctrl {
  border-radius: 8px;
  /* 可选: 让边角变圆 */
  background-color: white;
  margin-top: 2vh;
  margin-bottom: 4vh;
  display: flex;
  /* 启用 Flexbox */
  flex-direction: column;
  /* 设置主轴方向为纵向 */
  justify-content: space-evenly;
  /* 在主轴上均匀分配子元素的间距 */
}

.bottom-ctrl-one {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.bottom-button {
  height: 8vh;
  width: 15vw;
  font-size: 1.5vw;
  margin-left: 0.5vw;
  margin-right: 0.5vw;
}

.upload {
  display: flex;
  flex-direction: column;
  /* 垂直方向排列子元素 */
  justify-content: center;
  /* 垂直方向居中对齐 */
  align-items: center;
  /* 水平方向居中对齐 */
  height: 100%;
  /* 确保容器有足够的高度 */
  width: 100%;
  /* 确保容器有足够的宽度 */
  border: 1px dashed #dcdfe6;
  /* 可选: 只是为了视觉效果 */
  border-radius: 8px;
  /* 可选: 让边角变圆 */
  margin-bottom: -2vh;
}

.upload div {
  width: 100%;
  height: 100%;
}

.upload .el-icon-upload {
  font-size: 20vw;
  /* 图标的大小 */
  margin-bottom: 3vh;
  /* 图标与文本之间的间距 */
}

.upload .el-upload__text {
  text-align: center;
  /* 确保文本内容在其容器中居中对齐 */
  font-size: 3vw;
}

/deep/ .el-upload {
  width: 100%;
  height: 100%;
}

/deep/ .el-upload .el-upload-dragger {
  width: 100%;
  height: 100%;
}

.main {
  display: flex;
  height: 90%;
  width: 100%;
  justify-content: space-evenly;
}

.drawer-open {
  position: fixed;
  top: 7vh;
  right: 0;
  width: 15vw;
  height: 100%;
  background-color: #fff;
  box-shadow: -1px 0 2px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  transform: translateX(0);
  transition: transform 0.3s ease;
  user-select: none;
}

.drawer-close {
  position: fixed;
  top: 7vh;
  right: 0;
  width: 15vw;
  height: 100%;
  background-color: #fff;
  box-shadow: -1px 0 2px rgba(0, 0, 0, 0.5);
  z-index: 1000;
  transform: translateX(14vw);
  transition: transform 0.3s ease;
  user-select: none;
}

.drawer-content {
  height: 100%;
  overflow-y: auto;
}

.drawer-content::-webkit-scrollbar {
  display: none;
  /* 针对 Chrome, Safari, Edge 浏览器隐藏滚动条 */
}

.drawer-button-bar {
  background-color: white;
  height: 100%;
  width: 1vw;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.drawer-button-open {
  position: absolute;
  top: 50%;
  left: 100%;
  /* 将按钮放在抽屉外侧 */
  transform: translate3d(-100%, -50%, 0);
  transition: all 0.5s;
}

.drawer-button-close {
  position: absolute;
  top: 50%;
  left: 100%;
  /* 将按钮放在抽屉外侧 */
  transform: translate3d(-100%, -50%, 0) rotateX(180deg);
  transition: all 0.5s;
}

video::-webkit-media-controls-play-button {
  display: none;
}

video::-webkit-media-controls-timeline {
  display: none;
}
</style>



